import React from "react";

import { Theme } from "../../types/common.type";

interface IButtonProps extends React.ButtonHTMLAttributes<HTMLButtonElement> {
  theme: Theme;
  onClick?: () => any;
  className?: string;
  border?: boolean;
}

const Button = (props: React.PropsWithChildren<IButtonProps>) => {
  const { theme, onClick, className, children, border, ...attr } = props;

  const colorClasses = `${
    theme === Theme.dark
      ? "bg-main-100 text-secondary-100 border-secondary-100 hover:bg-main-70"
      : "bg-secondary-100 text-main-100 border-main-100 hover:bg-secondary-30"
  }`;

  return (
    <button
      onClick={onClick}
      className={`flex min-w-10 items-center justify-center rounded-lg  border-solid ${
        border ? "border" : ""
      } ${colorClasses} ${className}`}
      {...attr}
    >
      {children}
    </button>
  );
};

export default Button;
