import React from "react";
import { Link } from "gatsby";

import { Theme } from "../../types/common.type";

import Button from "./button";

const Pagination = ({
  section,
  pagesQuantity,
  currentPage,
  nextPageTitle,
  className,
}: {
  section: "projects" | "blog";
  pagesQuantity: number;
  currentPage: number;
  nextPageTitle?: string;
  className?: string;
}) => {
  const pages = Array.from({ length: pagesQuantity }, (_, i) => i + 1);
  const nextPage = currentPage + 1;

  return (
    <div className={`flex justify-center w-full gap-6 ${className}`}>
      {currentPage < pagesQuantity ? (
        <Link to={`/${section}/page/${nextPage}`} className="w-max shrink-0">
          <Button
            theme={Theme.light}
            border
            className="whitespace-nowrap h-10 px-5 font-bold"
          >
            {nextPageTitle ?? "Next page"}
          </Button>
        </Link>
      ) : null}

      <nav className="shrink-0 flex gap-2">
        {pagesQuantity > 1
          ? pages.map((page) => (
              <Link
                key={`pagination-link-${page}`}
                to={`/${section}/page/${page}`}
              >
                <Button
                  theme={Theme.light}
                  border={page === currentPage}
                  className="h-10 font-bold"
                >
                  {page}
                </Button>
              </Link>
            ))
          : null}
      </nav>
    </div>
  );
};

export default Pagination;
